<template>
<div>
	<router-link to='/time-entries/log-time'>
		<button
	      v-if="$route.path !== '/time-entries/log-time'" class="btn btn-primary">创建
	    </button>
	</router-link>

    <div v-if="$route.path === '/time-entries/log-time'">
      <h3>创建</h3>
    </div>

    <hr>

   
    <router-view></router-view>

    <div class="time-entries">
      <p v-if="!timeEntriesCopy.length"><strong>还没有任何任务</strong></p>

      <div class="list-group">
        
        <a class="list-group-item" v-for="timeEntry in timeEntriesCopy">
          <div class="row">

            <div class="col-sm-2 user-details">
              <img :src="timeEntry.user.image" class="avatar img-circle img-responsive" />
              <p class="text-center">
                <strong>
                  {{ timeEntry.user.name }}
                </strong>
              </p>
            </div>

            <div class="col-sm-2 text-center time-block">
              <h3 class="list-group-item-text total-time">
                <i class="glyphicon glyphicon-time"></i>
                {{ timeEntry.totalTime }}
              </h3>
              <p class="label label-primary text-center">
                <i class="glyphicon glyphicon-calendar"></i>
                {{ timeEntry.date }}
              </p>
            </div>

            <div class="col-sm-7 comment-section">
              <p>{{ timeEntry.comment }}</p>
            </div>

            <div class="col-sm-1">
              <button
                class="btn btn-xs btn-danger delete-button"
               
                @click="deleteTimeEntry(timeEntry)">
                X
              </button>
            </div>

          </div>
        </a>

      </div>
    </div>
  </div>	
</template>
<script>
	export default {
	    methods: {
	      deleteTimeEntry (timeEntry) {
	        //这个方法用于删除某一项计划
	        let index = this.$parent.timeEntries.indexOf(timeEntry)
	        if (window.confirm('确定要删除吗?')) {
	          this.$parent.timeEntries.splice(index, 1)
	        }
	      }
	    },
	    computed:{
	    	timeEntriesCopy:function() {
	    		return this.$parent.timeEntries;
	    	}
	    }
	}
</script>
<style>
	.avatar {
	    height: 75px;
	    margin: 0 auto;
	    margin-top: 10px;
	    margin-bottom: 10px;
	  }
	  .user-details {
	    background-color: #f5f5f5;
	    border-right: 1px solid #ddd;
	    margin: -10px 0;
	  }
	  .time-block {
	    padding: 10px;
	  }
	  .comment-section {
	    padding: 20px;
	  }	
</style>


